* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  font-family: 'Montserrat' !important;
  background-color: #fff !important;
  min-height: 100%;
  height: 100%;
  max-width: 100%;
  width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.xl {
  font-size: 2.25rem;
  font-weight: 700;
}

.lg {
  font-size: 1.4rem;
  font-weight: 600;
}

.md {
  font-size: 1rem;
  font-weight: 500;
}

.sm {
  font-size: 0.75rem;
  font-weight: 400;
}

.xs {
  font-size: 0.6rem;
  font-weight: 300;
}

@media (min-width: 414px) {
  .xl {
    font-size: calc(2.25rem + (((100vw - 20rem) / (90 - 20))) * (4.75 - 2.25));
  }

  .lg {
    font-size: calc(1.4rem + ((1.8 - 1.4) * ((100vw - 20rem) / (90 - 20))));
  }

  .md {
    font-size: calc(1rem + ((1.25 - 1) * ((100vw - 20rem) / (90 - 20))));
  }

  .sm {
    font-size: calc(0.75rem + ((0.9 - 0.75) * ((100vw - 20rem) / (90 - 20))));
  }

  .xs {
    font-size: calc(0.6rem + ((0.8 - 0.6) * ((100vw - 20rem) / (90 - 20))));
  }
}

@media (min-width: 1440px) {
  .xl {
    font-size: 4.75rem;
  }

  .lg {
    font-size: 2.5rem;
  }

  .md {
    font-size: 1.25rem;
  }

  .sm {
    font-size: 0.9rem;
  }

  .xs {
    font-size: 0.8rem;
  }
}

/************************* TEMPLATE STYLES *************************/

.section-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
