  transition: opacity 0.5s ease-in-out;
}

.content-preload {
  opacity: 0;
}

/************************* PRELOADER *************************/
#preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 9999;
}

#preloader.fadeout {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.5s, opacity 0.5s linear;
}

#preloader-container {
  position: absolute;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-hourglass:after {
  content: ' ';
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid #253069;
  border-color: #253069 transparent #253069 transparent;
  -webkit-animation: lds-hourglass 1.2s infinite;
          animation: lds-hourglass 1.2s infinite;
}
@-webkit-keyframes lds-hourglass {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
            animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    -webkit-transform: rotate(900deg);
            transform: rotate(900deg);
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    -webkit-transform: rotate(1800deg);
            transform: rotate(1800deg);
  }
}
@keyframes lds-hourglass {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
            animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    -webkit-transform: rotate(900deg);
            transform: rotate(900deg);
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    -webkit-transform: rotate(1800deg);
            transform: rotate(1800deg);
  }
}

/************************* NAVBAR STYLES *************************/
#navbar {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 99;
  background-color: #fff;
  box-shadow: rgba(2, 12, 27, 0.7) 0px 10px 30px -10px;
}

#navbar img {
  margin: 12px 0px 12px 10px;
  max-width: 45px;
  min-width: 45px;
  width: 100%;
  height: auto;
}

.nav-text {
  display: none;
  width: 100%;
  margin-left: auto;
  padding-right: 10px;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.nav-text a {
  padding: 5px 10px;
  text-decoration: none;
  color: black;
  transition: color 0.5s ease; /* Transition effects on hover (color) */
}

/* When you mouse over the navigation links, change their color */
.nav-text a:hover,
.nav-text a:focus {
  color: #fcba04;
}

.nav-menu {
  margin-left: auto;
  padding-right: 10px;
  font-size: 25px;
}

/* The Overlay (background) */
#nav-overlay {
  height: 100%;
  width: 0;
  position: fixed; /* Stay in place */
  z-index: 110; /* Sit on top */
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.95); /* Black w/opacity */
  overflow-x: hidden;
  overflow-y: hidden;
  transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
}

/* Position the content inside the overlay */
.nav-overlay-content {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* The navigation links inside the overlay */
#nav-overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block; /* Display block instead of inline */
  transition: color 0.5s ease; /* Transition effects on hover (color) */
}

/* When you mouse over the navigation links, change their color */
#nav-overlay a:hover,
#nav-overlay a:focus {
  color: #fcba04;
  font-weight: 600;
  text-decoration: none;
}

/* Position the close button (top right corner) */
#nav-overlay .icon-close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  color: white;
  font-size: 25px;
  transition: color 0.5s ease; /* Transition effects on hover (color) */
}

#nav-overlay .icon-close:hover {
  color: #fcba04;
}

body.nav-open {
  /* disable scroll when modal is active */
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .nav-menu {
    display: none;
  }

  .nav-text {
    display: flex;
  }
}

/************************* INTRO STYLES *************************/

.about-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  z-index: 2;
}

.about-container h3 {
  background: -webkit-linear-gradient(#fcba04, #009ffd);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.typewriter {
  width: 100%;
  font-weight: 700;
  text-align: left;
  -ms-word-break: break-word;
  word-break: break-word;

  /* Non standard for webkit */
  overflow-wrap: break-word;
  word-wrap: break-word;

  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}

.typewriter span {
  letter-spacing: 0.08rem;
}

.typewriter-text > span {
  color: #fcba04;
  border-right: 0.15em solid lightgrey; /* The typwriter cursor */
  /* margin: 0 auto; Gives that scrolling effect as the typing happens */
}

.typewriter-text.blink > span {
  -webkit-animation: blink-caret 1s step-end infinite;
          animation: blink-caret 1s step-end infinite;
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: lightgrey;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: lightgrey;
  }
}

.about-text {
