.resume-button {
  border: 1px solid #009ffd;
  background-color: transparent;
  width: 50%;
  height: 50px;
  margin-bottom: 50px;

  /* "left to right" */
  background: linear-gradient(to left, transparent 50%, #21295c 50%) right;
  background-size: 200%;
  transition: all 0.5s ease-out;
}

@media screen and (min-width: 769px) {
  .resume-button {
    width: 35%;
  }
}

@media screen and (min-width: 1024px) {
  .resume-button {
    width: 25%;
  }
}

.resume-button:hover {
  background-position: left;
}

.resume-button a {
  text-decoration: none;
  color: black;
  transition: 0.5s ease-out;
}

.resume-button:hover a {
  color: white;
}

