  }
}

@media (min-width: 631px) and (max-width: 1150px) {
  .projects {
    grid-template-columns: repeat(2, 1fr);
  }
  .proj-item {
    height: 22rem;
  }
}

@media (min-width: 1151px) {
  .projects {
    grid-template-columns: repeat(3, 1fr);
  }
